import { useState, useEffect, useCallback } from "react";
import {
  getAllDealOfTheDay,
  getAllFeaturedDeal,
  getAllFlashDeal,
  getDealOfTheDayById,
  getFeaturedDealById,
  getFlashDealById,
} from "../api/dealApis";

const useDeal = (dealName = "flash", dealId) => {
  const [deals, setDeals] = useState([]);
  const [deal, setDeal] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchAllDeals = useCallback(async () => {
    if (dealId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `${dealName}_deals`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setDeals(JSON.parse(cachedData));
      } else if (dealName === "flash") {
        const response = await getAllFlashDeal();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.flashDealDetails)
          );
          setDeals(response.data.flashDealDetails);
        }
      } else if (dealName === "featured") {
        const response = await getAllFeaturedDeal();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.featuredDealsDetails)
          );
          setDeals(response.data.featuredDealsDetails);
        }
      } else if (dealName === "dealOfTheDay") {
        const response = await getAllDealOfTheDay();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.dealOfTheDayDetails)
          );
          setDeals(response.data.dealOfTheDayDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [dealId, dealName]);

  const fetchDeal = useCallback(async () => {
    if (!dealId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `${dealName}_deal_${dealId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setDeal(JSON.parse(cachedData));
      } else if (dealName === "flash") {
        const response = await getFlashDealById(dealId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.dealDetails)
          );
          setDeal(response.data.dealDetails);
        }
      } else if (dealName === "featured") {
        const response = await getFeaturedDealById(dealId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.dealDetails)
          );
          setDeal(response.data.dealDetails);
        }
      } else if (dealName === "dealOfTheDay") {
        const response = await getDealOfTheDayById(dealId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.dealDetails)
          );
          setDeal(response.data.dealDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [dealId, dealName]);

  useEffect(() => {
    fetchAllDeals();
  }, [fetchAllDeals]);

  useEffect(() => {
    fetchDeal();
  }, [fetchDeal]);

  return {
    deals,
    deal,
    loading,
    error,
    refresh: fetchAllDeals,
  };
};

export default useDeal;
