import useMediaQuery from "../hooks/useMediaQuery";
import { useBanners } from "../services/bannerServices";
import Carousel from "./Carousel";
import Skeleton from "./Skeleton";

const SmallRectangle = ({
  position,
  resourceType,
  valueId,
  title,
  subtitle,
  className,
  containerClassName = "container max-w-full",
}) => {
  const { data: banners = [], isLoading } = useBanners({
    bannerType: "Small Rectangle",
    position,
    resourceType,
    valueId,
  });
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");

  if (isLoading) {
    return (
      <ul className={`flex gap-4 overflow-x-hidden ${containerClassName}`}>
        {Array.from({ length: 10 }).map((_, index) => (
          <li
            key={index}
            className={`w-[390px] max-xl:w-[330px] max-lg:w-[300px] h-[520px] max-xl:h-[440px] max-lg:h-[400px] rounded-lg overflow-hidden flex-shrink-0 ${className}`}
          >
            <Skeleton />
          </li>
        ))}
      </ul>
    );
  }

  return (
    banners?.length > 0 && (
      <div
        className={`space-y-4 max-lg::space-y-2 max-md:pr-0 ${containerClassName}`}
      >
        {title || subtitle ? (
          <div>
            <h1 className="text-2xl max-lg:text-xl font-bold">{title}</h1>
            <p className="text-sm">{subtitle}</p>
          </div>
        ) : null}
        <Carousel
          slideWidth={maxLg ? 300 : maxXl ? 330 : 390}
          navigation
          autoplay
          delay={5000}
          spaceBetween={maxLg ? 10 : 14}
          loop
          allowTouchMove
        >
          {banners.map((banner) => (
            <a
              key={banner.bannerTitle || banner._id}
              href={banner.linkUrl}
              className="inline-block relative"
            >
              <img
                src={banner.bannerImg}
                alt={banner.name || banner._id}
                loading="lazy"
                className={`w-[390px] max-xl:w-[330px] max-lg:w-[300px] h-[520px] max-xl:h-[440px] max-lg:h-[400px] object-cover object-center rounded-lg ${className}`}
              />
            </a>
          ))}
        </Carousel>
      </div>
    )
  );
};

export default SmallRectangle;
